import simpleAddress from '@/components/simple-address/simple-address.vue';
export default {
	components: {
	    simpleAddress
	},
	data() {
		return {
			cityPickerValueDefault: [0, 0, 0],
			pickerText: '请选择',
			id:0,
			my:[],
		};
	},
	onLoad(options){
		this.id = options.id
		this.myPrizeDetail();
	},
	onShow(){
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
	},
	methods: {
		
		myPrizeDetail(){
			let self = this;
			var path = '/gzh/activity_api/my_prize_detail'
			var data = {id:self.id};
			getApp().http(path, data, function(res) {
				self.my = res;
				console.log('向平',res)
			});
		},
		indexUrl(icon){
			let http = icon.indexOf('http')
			if(http == -1){
				return this.webUrl +'/'+icon
			}else{
				return icon;
			}
		},
		
		openAddres() {
			this.$refs.simpleAddress.open();
		},
		onConfirm(e) {
			console.log(e);
			this.pickerText = e.label
		},
		
		formSubmit(e){
			console.log('dsdf ',e)
			let self = this;
			let data = e.detail.value;
			if(!data.name){
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.tel)){
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			if(!self.pickerText){
				this.$api.msg('请输入地址');
				return;
			}
			if(!data.det){
				this.$api.msg('请填写门牌号信息');
				return;
			}
			// getApp().showTip('none');
			
			
			uni.showModal({
				title: '完善信息',
				content: '确定后信息无法修改，请确定信息填写是否正确',
				success: (md) => {
					console.log('sssss')
					if(md.confirm == true){
						console.log('确定')
						
						let path = '/gzh/activity_api/up_prize_info';
						let datas ={
							id:self.id,
							realname:data.name,
							tel:data.tel,
							addr:self.pickerText
						}
						getApp().http(path, datas, function(res) {
							console.log('修改',res);
							getApp().showTip('完善信息成功','none');
							self.myPrizeDetail();
						},false,'post');
						
					}
				}
			})
			
			
			
			
			
		}
		
	},
}
